<template>
  <div>
        <h3 class="text-center">Todos os Administradores</h3>
        <div v-if="message" class="alert alert-success">{{this.message}}</div>
        <div class="container">
            <table class="table table-borderless table-striped table-image mt-4">
                <thead>
                    <tr>
                        <th class="text-center">ID</th>
                        <th>Nome</th>
                        <th>Permissão</th>
                        <th class="text-center">Editar</th>
                        <th class="text-center">Deletar</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="administrador in administradores" v-bind:key="administrador.cdUsuario">
                        <td class="text-center">{{administrador.cdUsuario}}</td>
                        <td>{{administrador.nome}}</td>
                        <td>{{administrador.permissoes}}</td>
                        <td class="text-center"><button class="btn btn-success" v-on:click="update(administrador.cdUsuario)"><i class="material-icons">edit</i></button></td>
                        <td class="text-center"><button class="btn btn-warning" v-on:click="remove(administrador.cdUsuario)"><i class="material-icons">delete</i></button></td>
                    </tr>
                </tbody>
            </table>
            <div class="row">
                <button class="btn btn-success" v-on:click="add()">Add</button>
            </div>
        </div>
    </div>
</template>

<script>
import DataService from '../service/AdministradorDataService';
export default {
    name: "Administradores",
    data() {
        return {
            administradores: [],
            message: ""
        }
    },
    methods: {
        refresh() {
            DataService.retrieveAll()
            .then((response) => {
                this.administradores = response.data;
            });
        },
        add() {
            this.$router.push(`/administradores/add`);
        },
        update(id) {
            this.$router.push(`/administradores/${id}`);
        },
        remove(id) {
            DataService.remove(id)
            .then(() => {
                this.refresh();
            }).catch(e => {
              console.log(e)
              if(e.response){
                this.$store.dispatch('setSnackbar', {
                  showing: true, text: e.response.data.message, timeout: 8000, color: 'error'
                });
              }
            });
        },
    },
    created() {
        this.refresh();
    }
}
</script>

